.events-container {
    position: relative;
    max-width: 100%;
    vertical-align: middle;
    display: flex;
    flex-direction: column;
}

.events-wrapper {
    padding: 20px 0;
}

.event-header {
    margin-bottom: 8px;
}

.event-image-link {
    margin-bottom: 6px;
    display: block;
}

.event-image {
    width: 100%;
    height: auto;
    max-width: 100%;
    box-sizing: border-box;
    aspect-ratio: auto 600 / 371;
    vertical-align: middle;
}

.event-title {
    text-decoration: none;
    cursor: pointer;
    margin-top: 0;
}

.event-link {
    width: 100%;
    font-family: "Montserrat", Helvetica, sans-serif;
    color: #000;
    text-transform: none;
    margin-top: 0;
    margin-bottom: 6px;
    text-decoration: none;
    display: block;
}

.event-description {
    margin-top: 0;
}

.event-footer {
    display: flex;
    flex-wrap: wrap;
    position: relative !important;
    overflow: hidden;
}

.event-date {
    display: inline-block !important;
    position: relative !important;
    margin-top: 0;
    margin-bottom: 10px;
}

.event-bar {
    margin-bottom: 8px;
    align-items: center;
    align-content: center;
    text-align: center;
    border-bottom: 1px solid var(--dark-gray-color);
    color: var(--dark-gray-color);

}

.event-bar-link {
    text-decoration: none;
    font-size: 16px;
    font-family: "Montserrat", Helvetica, sans-serif;
    color: var(--light-gray-color);
    line-height: 1.5;
    font-weight: 600;
}

.event-bar-span {
    padding: 3px;
    font-size: 8px;
    margin-bottom: 6px;
}

.sponsor-event {
    max-width: 90%;
}

.event-link:hover {
    color: var(--primary-color);
}
.event-bar-link:hover {
    color: var(--primary-color);
}


@media (min-width: 998px) {
    .events-container {
        padding-right: 0;
        padding-left: 9px;
        width: 48.68%;
    }
}

@media (min-width: 480px) {

    .event-bar-span {
        padding: 10px;
        font-size: 12px;
    }
}

@media (min-width: 400px) {
    .event-image-link {
        margin-bottom: 7px;
    }

    .event-title {
        margin-bottom: 9px;
    }
    .event-link  {
        font-size: 12px;
        line-height: 1.15em;
    }

    .event-description {
        font-size: 12px;
        line-height: 1.2415;
        margin-bottom: 7px;
    }

    .event-footer {
        font-size: 11px;
        line-height: 1.19375;
        margin-bottom: 9px;
    }

    .event-bar-link {
        font-size: 12px;
    }
}

@media (min-width: 640px) {
    .event-image-link {
        margin-bottom: 8px;
    }

    .event-title {
        margin-bottom: 10px;
    }

    .event-link  {
        font-size: 1.1rem;
        line-height: 1.15em;
    }

    .event-description {
        font-size: 14px;
        line-height: 1.3;
        margin-bottom: 8px;
    }
    .event-footer {
        font-size: 14px;
        line-height: 1.25;
        margin-bottom: 10px;
    }

    .event-bar-link {
        font-size: 16px;
    }

}