.header {
    width: 100%;
    height: fit-content;
    box-sizing: border-box;
    background-color: white;
    position: sticky;
    right: 0;
    z-index: 2;
}

.header-container {
    width: 100%;
    max-width: 1020px;
    margin: auto;
    box-sizing: border-box;
}

.header-wrapper {
    position: relative;
    padding: 10px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    overflow: visible;
    height: 90px;
}

.margin-remove {
    margin: 0 !important;
    padding-bottom: 5px;
}

.link-logo {
    min-width: 230px;
    vertical-align: bottom;
    height: auto;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
}

.logo {
    top: 9px;
    text-align: left;
    max-width: 230px;
    width: 100%;
    vertical-align: bottom;
    transition: 188ms ease-out;
}

.sponsor {
    margin: 0 !important;
}

@media (max-width: 998px) {
    .header-wrapper {
        align-items: center;
    }
    .logo {
        text-align: center;
        margin-bottom: 10px;
        width: 90%;
    }
    .sponsor {
        display: none;
    }
    .header-container {
        box-shadow: 0 0 12px rgba(0, 0, 0, 0.24);
    }
}

@media (max-width: 480px) {
    .header-wrapper {
        padding: 0;
        height: 45px;

    }

    .logo {
        text-align: left;
        margin-bottom: 0;
        width: 35%;
    }

    .margin-remove {
        padding-bottom: 0;
    }


}